:root {
  --clr-txt-1: hsla(0, 0%, 0%, 1);
  --clr-txt-2: hsla(0, 0%, 48%, 1);
  --clr-neutral-1: hsla(0, 0%, 100%, 1);
  --clr-neutral-2: hsla(0, 0%, 98%, 1);
  --clr-neutral-3: rgba(0, 0, 0, 0.15);
  --clr-accent: hsla(176, 50%, 47%, 1);
  --clr-accent-dark: hsla(176, 72%, 28%, 1);
  --clr-accent-disable: hsla(0, 0%, 18%, 1);
  --clr-border: hsla(0, 0%, 18%, .1);
  --clr-menu-bg-1: hsla(0, 0%, 0%, 1);
  --clr-menu-bg-2: hsla(0, 0%, 0%, 0);
  --ff-commissioner: Commissioner, sans-serif;
}

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  min-height: 100vh;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

h1, h2, h3 {
  color: var(--clr-txt-1);
}

h1 {
  font-size: 1.25rem;
  line-height: 1.223;
}
@media (min-width: 75rem) {
  h1 {
    font-size: 1.25rem;
    line-height: 1.223;
  }
}

h2 {
  font-size: 1.125rem;
  line-height: 1.2222222222;
}
@media (min-width: 75rem) {
  h2 {
    font-size: 1.25rem;
    line-height: 1.223;
  }
}

h3 {
  font-size: 0.875rem;
  line-height: 1.2228571429;
}
@media (min-width: 75rem) {
  h3 {
    font-size: 1.125rem;
    line-height: 1.2222222222;
  }
}

a {
  text-decoration: none;
}

body {
  font-family: var(--ff-commissioner);
  color: var(--clr-txt-2);
  font-size: 0.875rem;
  line-height: 1.7142857143;
}
@media (min-width: 75rem) {
  body {
    font-size: 1rem;
    line-height: 1.875;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
#bar1,
#bar2,
#bar3 {
  transition: all 250ms ease;
}

.navbar.open #bar2 {
  opacity: 0;
}
.navbar.open #bar1 {
  transform-origin: -1px 3px;
  transform: rotate(45deg);
}
.navbar.open #bar3 {
  transform: rotate(-45deg);
  transform-origin: 1px 13px;
}

.container {
  width: min(100%, 69.375rem);
  margin-inline: auto;
  padding-inline: 1.5rem;
}
@media (min-width: 75rem) {
  .container {
    padding-inline: 0;
  }
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 2rem;
}
@media (min-width: 45rem) {
  .navbar {
    padding-block: 3rem;
  }
}
.navbar.open .navbar__menu {
  display: block;
  animation: fade-in 300ms forwards;
}
@media (min-width: 45rem) {
  .navbar.open .navbar__menu {
    display: flex;
  }
}
.navbar.open .navbar__overlay {
  display: block;
  animation: fade-in 250ms forwards;
}
@media (min-width: 45rem) {
  .navbar.open .navbar__overlay {
    display: none;
  }
}
.navbar__toggler {
  background: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 45rem) {
  .navbar__toggler {
    display: none;
  }
}
.navbar__menu {
  display: none;
  list-style: none;
  position: absolute;
  top: 5.5rem;
  right: 1.5rem;
  left: 1.5rem;
  background-color: var(--clr-neutral-1);
  border-radius: 0.5rem;
}
@media (min-width: 45rem) {
  .navbar__menu {
    position: static;
    background-color: inherit;
    border-radius: none;
    display: flex;
    gap: 2rem;
  }
}
.navbar__item:not(:last-child) {
  border-bottom: 1px solid var(--clr-border);
}
@media (min-width: 45rem) {
  .navbar__item:not(:last-child) {
    border: none;
  }
}
.navbar__link {
  color: var(--clr-txt-1);
  font-size: 1.125rem;
  font-weight: 500;
  display: block;
  padding: 1.5rem;
  position: relative;
}
@media (min-width: 45rem) {
  .navbar__link {
    color: var(--clr-neutral-1);
    font-size: 0.8125rem;
    padding: 0;
    display: inline-block;
  }
}
.navbar__link:hover::after {
  width: 100%;
}
.navbar__link::after {
  content: "";
  position: absolute;
  bottom: 3px;
  left: 50%;
  transform: translateX(-50%);
  height: 1px;
  width: 0;
  background-color: var(--clr-neutral-1);
  transition: all 200ms linear;
}
.navbar__overlay {
  display: none;
  position: absolute;
  inset: 0;
  z-index: -1;
  background: linear-gradient(180deg, var(--clr-menu-bg-1) 0%, var(--clr-menu-bg-2) 100%);
}

body {
  background: var(--clr-neutral-2);
}